@charset "UTF-8";
/* DEV — affichage du contour des blocs */
.barrnav {
  background-color: #0002;
  color: #000;
}

.barrnav a {
  color: inherit;
}

.bienvenue {
  display: flex;
  align-items: center;
  height: 88px;
  margin-left: 14px;
}

.hamb, .checkMenu {
  width: 60px;
  height: 60px;
  position: fixed;
  top: 14px;
  right: 14px;
}

.checkMenu {
  opacity: 0;
}

ul.menu {
  display: none;
}

input[type='checkbox']:checked ~ ul.menu {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0px;
}

input[type='checkbox']:checked ~ ul.menu li {
  margin: 10px;
  text-align: center;
}
